import "../styles/main.css";

import   React                                    from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Header      from "./templates/header.js";
import Footer      from "./templates/footer.js";
import NavBar      from "./navbar.js";
import Profile     from "./profile.js";
import Timeline    from "./timeline.js";
import DevLogs     from "./devlogs.js";
import Test        from "./test.js";
import ContactList from "./templates/contact_list.js";

class Main extends React.Component
{
    render()
    {
        <Router></Router>;  // NOTE:    Needed to silence "Router not used"
                            //          warning. Also needed to be imported or
                            //          Route would not work properly.

        return (
            <div className="Main">
                <Header/>
                <Switch>
                    <Route path="/profile"  component={Profile }></Route>
                    <Route path="/timeline" component={Timeline}></Route>
                    <Route path="/dev_logs" component={DevLogs }></Route>
                    <Route path="/.test"    component={Test    }></Route>
                </Switch>
                <ContactList/>
                <Footer/>
                <NavBar/>
            </div>
        );
    }
}

export default Main;
