import Canvas from "./canvas.js";

class HorizontalLine extends Canvas
{
    thickness = 10;

    componentDidMount()
    {
        const canvas  = this.myRef.current;
        const context = canvas.getContext('2d');

        this.resize(canvas, context);

        const canvas_width  = context.canvas.width;
        const canvas_height = context.canvas.height;
        const y             = Math.round((canvas_height - this.thickness) * 0.5);

        context.clearRect(0, 0, canvas_width, canvas_height);
        context.fillStyle = this.colour;
        context.fillRect(0, y, canvas_width, this.thickness);
    }
}

export default HorizontalLine;
