import "../styles/timeline.css";

import React          from "react";
import EosBrief       from "./briefs/eos_brief.js";
import Circle         from "./templates/circle.js";
import Rect           from "./templates/rect.js";
import VerticalLine   from "./templates/vertical_line.js";
import HorizontalLine from "./templates/horizontal_line.js";

class Test extends React.Component
{
    render()
    {
        return (
            <div className="Timeline">
                <div style={{width:"90%", display:"flex", flexDirection:"row", margin:"auto"}}>
                    <div style={{display:"flex", flexDirection:"column", margin:"auto"}}>
                        <div style={{width:"25px", height:"25px" , margin:"auto"}}><Circle        ></Circle        ></div>
                        <div style={{width:"25px", height:"100px", margin:"auto"}}><Rect          ></Rect          ></div>
                        <div style={{width:"25px", height:"100px", margin:"auto"}}><VerticalLine  ></VerticalLine  ></div>
                        <div style={{width:"25px", height:"100px", margin:"auto"}}><HorizontalLine></HorizontalLine></div>
                    </div>
                    <div className="Slot Project-Left"><EosBrief /></div>
                </div>
            </div>
        );
    }
}

export default Test;
